<template>
  <div id="add-blog">
      <h2>Blogging</h2>
      <form v-if="!submmited">
          <label for="">
              Title:
          </label>
          <input type="text" v-model="blog.title" required>
          <label for="">Content:</label>
          <textarea v-model="blog.content" name="" id="" cols="30" rows="10"></textarea>
          <div id="checkboxs">
              <label for="">Diary:</label>
              <input type="checkbox" value="Diary:" v-model="blog.categories">
                            <label for="">Extract</label>
              <input type="checkbox" value="Extract" v-model="blog.categories">
                            <label for="">Technology</label>
              <input type="checkbox" value="Technology" v-model="blog.categories">
                            <label for="">Other</label>
              <input type="checkbox" value="Other" v-model="blog.categories">    
          </div>
          <label for="">Author:</label>
          <select v-model="blog.author">
              <option v-for="author in authors">{{author}} </option>
          </select>
            <button v-on:click.prevent="post">Add</button>
              <!-- .prevent 可以阻止刷新页面 -->
      </form>
      <hr>
     <div v-if="submmited"><h3>Your blog was published successfully.</h3></div>
      <div id="preview">
          <h3>Overview</h3>
          <p> Title:{{blog.title}}</p>
          <p>Content:</p>
          <p class="content">{{blog.content}}</p>
           <p>Category:
              <ul>
                  <li v-for="category in blog.categories">{{category}}</li>
              </ul>
              </p>
              <p>Author:{{blog.author}}</p>
            
      </div>
  </div>
</template>

<script>
 
export default {

  name: 'add-blog',
  data () {
    return {
        blog:{
            title:'',
            content:'',
            categories:[],
            author:''
        },
        authors:['Fulian Li','Friends','Others'],
        submmited:false,
    }
  },
  methods:{
          //1.es6方法。2.使用了vue-resourse 需要安装
        post:function(){
            this.$http.post("https://myblogs-123.firebaseio.com/posts.json",this.blog).then(function(data){
                console.log(data);
                this.submmited=true;
            })
        }
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
    box-sizing: border-box;
}
#add-blog{
    margin: 40px auto;
    max-width: 600px;
}
label{
    display: inline-block;
    font-size: 18px;
}
input[type=text]{
    width: 100%;
    height: 40px;
    margin: 10px 0 30px 0;
}
textarea{
    margin: 10px 0;
    width: 100%;
    min-height: 200px;
    max-height: 400px;
}
#checkboxs{
    margin-bottom: 30px;
}
#checkboxs label{
    display: inline-block;
}
#checkboxs input{
    margin-right: 20px;
    width: 20px;
    height: 20px;
}
select{
    width: 100px;
    height: 35px;
    margin-bottom:20px; 
}
button{
    float: right;
    height: 38px;
    width: 130px;
    font-size: 22px;
    background-color: greenyellow;
    color:rgb(250, 61, 61);
    cursor: pointer;
}
.content{
    min-height:30px; 
}

#preview ul{
    list-style:none;
    display:inline;
}
#preview ul li{
    display: inline-block;
    margin: 0 10px;
}


</style>



