<template>
  <div v-theme:column="'wide'" id="show-blogs"><!-- 自定义指令，column参数，传字符串再加引号，数组，对象。有空格也会变id-->
      <h1>Overview</h1>
      <input id="search" type="text" v-model="search" placeholder="Search">
      <div v-for="blog in filteredBlogs" class="single-blog">
        <router-link v-bind:to="'/blog/' + blog.id"><!--需要v-bind绑定-->
             <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>        
        </router-link>
        <article>
            {{blog.content | snippet}}
        </article>
      </div>
  </div>
</template>

<script> 

 
export default {
  name: 'show-blogs', 
  data(){
      return{
          blogs:[],
          search:"",
      }
  },
  created(){
    //   this.$http.get("http://jsonplaceholder.typicode.com/posts")
    this.$http.get("https://myblogs-123.firebaseio.com/posts.json")//请求本地，仅改地址（只能放到static文件夹下）
      .then(function(data){
          return data.json();
        //   console.log(data)
        // this.blogs=data.body.slice(0,10)
        console.log(this.blogs)
      })
      .then(function(data){
          var blogsArray = [];
          for(let key in data){
            //   console.log(data[key]);
            data[key].id= key;
            blogsArray.push(data[key]);
         }
        //  console.log(blogsArray);
        this.blogs = blogsArray;
        console.log(this.blogs)
      })
  },
  computed:{
      filteredBlogs:function(){
          return this.blogs.filter((blog) =>{
              return blog.title.match(this.search);//返回真假
          })
      }
  },
  filters:{
    //   "to-uppercase":function(value){
    //       return value.toUpperCase();
    //   }
         toUppercase(value){//es6写法
            return value.toUpperCase();
        }
    },
    directives:{//局部指令
        'rainbow':{
            bind(el,binding,vnde){
                el.style.color="#" +Math.random().toString(16).slice(2,8);
            }
        }
    }
}
</script>

<style>
#show-blogs{
    max-width: 800px;
    margin: 0 auto;
}
#show-blogs a{
    text-decoration: none;
}
.single-blog{
    padding:20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border-radius: 10px;
}
#search{
    min-width: 40%;
    max-width:600px;
    height: 40px;
    border-radius: 8px;
    padding: 5px 10px;
    font-size: 25px;
    /* margin:0 auto; */
}
</style>



